.Spinner_loader__3ENkM {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ee5555;
  background: linear-gradient(to right, #ee5555 10%, rgba(238,85,85, 0) 42%);
  position: relative;
  animation: Spinner_load3__jjSEM 1.4s infinite linear;
  transform: translateZ(0);
}
.Spinner_loader__3ENkM:before {
  width: 50%;
  height: 50%;
  background: #ee5555;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.Spinner_loader__3ENkM:after {
  background: #424249;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@keyframes Spinner_load3__jjSEM {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

